}

.notification-dismiss:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
}

/* Content Areas */
.content-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(139, 92, 246, 0.2);
}

.content-header h2 {
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 700;
}

.primary-button,
.secondary-button {
  padding: 0.8rem 1.5rem;
